body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.image-icon{
  width: 200px;
  height: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  background-repeat: no-repeat;
  margin: -100px 0 0 -100px;
}

.main{
	width: calc(100% - 100px);
  margin: auto;
}

.resturantRow{
	margin: 15px 0px;
}

.activeBadge{
	color: black;
	background-color: #fff;
}

.resturant-option{
	cursor: pointer;
}

.resturant-name{
  font-size: 17px;
  font-weight: 500;
  word-break: break-word;
}

.resturant-foodtypes{
  color: #686b78;
  font-size: 13px;
  margin-top: 4px;
  word-break: break-word;
}

.resturant{
  margin-top: 15px
}

.resturant-pic{
  height: 200px;
  width: 100%
}

.extra{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  margin-top: 15px;
  font-size: 12px;
  -ms-flex-pack: justify;
  justify-content: space-between;
  color: #535665;
}

.rating{
  background-color: #007bff;
  color: #fff;
  height: 20px;
  width: 43px;
  padding: 1px 4px;
  font-weight: 400;
}

.resturant-info{
  margin: 15px 0px;
}

.resturant-info:hover{
  border-color: #d3d5df;
  box-shadow: 0 4px 7px 0 rgba(218,220,230,.6);
  padding: 10px 20px 20px;
  border: 1px solid #fff;
  contain: content;
}

.buttonRow{
  align-items: center;
  justify-content: center;
  margin-top: 15px;
}

.list-group{
  position: fixed;
  width: 20%
}

.exclusive-resturant{
  background: rgb(250, 74, 91);
  color: rgb(255, 255, 255);
  border-color: rgb(209, 42, 59) transparent;
  position: absolute;
  left: 15px;
  top: 0;
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  padding: 5px 9px 4px;
  max-width: 50%;
  text-transform: uppercase;
}

.exclusive-resturant-label{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}